.nui-wrapper {
	text-align: center;
	height: 100vh;
	width: 100%;
}

.menu-wrapper {
	display: flex;
	justify-content: flex-end;
	height: 100%;
}

.menu {
	margin: 2rem;
	padding: 1rem;
	width: 30rem;
	background-color: rgba(0, 0, 0, 0.75);
	border-radius: 0.5rem;
	color: #fff;
	display: flex;
	flex-direction: column;
	position: relative;
}

.menu-header {
	border-bottom: 2px solid #fff;
}

.menu-content {
	display: flex;
	flex-direction: column;
	gap: 0.75rem;
	overflow-y: auto;
}

.menu-header > p {
	font-size: 0.8rem;
	margin-bottom: 0.5rem;
}

.menu-footer {
	border-top: 2px solid #fff;
}

.menu-content-item {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: rgba(0, 0, 0, 0.9);
	padding: 0.75rem;
	box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.menu-content-item-controls {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 1rem;
}

.arrow-icon {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 2rem;
	width: 2rem;
	border-radius: 0.25rem;
	background-color: rgba(90, 90, 90, 0.75);
	cursor: pointer;
}

button {
	background-color: rgba(90, 90, 90, 0.75);
	border: none;
	color: #fff;
	padding: 0.75rem 1.5rem;
	text-transform: capitalize;
	margin-top: 1rem;
	cursor: pointer;
	border-radius: 0.25rem;
	transition: all 0.1s ease-in-out;
}

button:hover {
	box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.menu-sidebar {
	position: absolute;
	left: -5rem;
	top: 0;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.menu-sidebar-inner {
	display: flex;
	flex-direction: column;
	gap: 1rem;
	/* background-color: rgba(0, 0, 0, 0.75); */
	padding: 0.5rem;
}

.sidebar-icon {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 3rem;
	width: 3rem;
	font-size: 1.25rem;
	border-radius: 0.5rem;
	background-color: rgba(0, 0, 0, 0.9);
	cursor: pointer;
}
